import Parser from "rss-parser";
import Link from "next/link";
import { Rss, Tag } from "lucide-react";

// ğŸ’¡ 1. ì‚¬ìš©ìê°€ ìš”ì²­í•œ ê³µì‹ Gemini ì œí’ˆ ë¸”ë¡œê·¸ RSS URL
const RSS_FEED_URL = "https://blog.google/products/gemini/rss/";

// ğŸ’¡ 2. í”¼ë“œ ì•„ì´í…œ íƒ€ì… ì •ì˜ (export í™•ì¸)
export type FeedItem = {
  title: string;
  link: string;
  pubDate: string;
  guid: string; // ğŸ‘ˆ ê³ ìœ  ID
  content: string; // ğŸ‘ˆ ì „ì²´ HTML ë³¸ë¬¸
  contentSnippet?: string; // ìš”ì•½ ë‚´ìš©
  categories?: string[]; // ì¹´í…Œê³ ë¦¬ (íƒœê·¸)
};

/**
 * Gemini ê³µì‹ RSS í”¼ë“œë¥¼ ê°€ì ¸ì˜¤ê³  íŒŒì‹±í•˜ëŠ” í•¨ìˆ˜ (export í™•ì¸)
 */
export async function getGeminiProductFeed() {
  try {
    // 1. Next.jsì˜ fetch
    const response = await fetch(RSS_FEED_URL, {
      next: { revalidate: 3600 },
    });

    if (!response.ok) {
      throw new Error(`Failed to fetch RSS feed: ${response.statusText}`);
    }

    const xmlText = await response.text();

    // 2. ESlint ì˜¤ë¥˜ë¥¼ í”¼í•˜ê¸° ìœ„í•´ Record<string, never> ì‚¬ìš©
    const parser = new Parser<Record<string, never>, FeedItem>();

    const feed = await parser.parseString(xmlText);

    const plainItems: FeedItem[] = feed.items.map((item) => {
      return {
        // FeedItem íƒ€ì…ì— ì •ì˜ëœ ì†ì„±ë§Œ ëª…ì‹œì ìœ¼ë¡œ ì¶”ì¶œ
        title: item.title,
        link: item.link,
        pubDate: item.pubDate,
        guid: item.guid,
        content: item.content,
        contentSnippet: item.contentSnippet,
        categories: item.categories,
      };
    });
    // ğŸ’¡ 3. ìˆœìˆ˜ ê°ì²´ ë°°ì—´(plainItems)ì„ ë°˜í™˜
    return plainItems;
  } catch (error) {
    console.error("Error fetching or parsing Gemini RSS feed:", error);
    return [];
  }
}

/**
 * Gemini ê³µì‹ í”¼ë“œë¥¼ ë Œë”ë§í•˜ëŠ” ì„œë²„ ì»´í¬ë„ŒíŠ¸
 */
export default async function GeminiProductFeed() {
  const items = await getGeminiProductFeed();

  if (items.length === 0) {
    return (
      <p className="text-gray-500">
        Google Gemini ê³µì‹ í”¼ë“œë¥¼ ë¶ˆëŸ¬ì˜¬ ìˆ˜ ì—†ìŠµë‹ˆë‹¤.
      </p>
    );
  }

  return (
    <div className="border border-gray-800 rounded-lg p-6 bg-gray-900/30">
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-2xl font-light">Gemini ê³µì‹ ë¸”ë¡œê·¸</h2>
        <a
          href={RSS_FEED_URL}
          target="_blank"
          rel="noopener noreferrer"
          title="View RSS Feed"
        >
          <Rss size={18} className="text-gray-500 hover:text-white" />
        </a>
      </div>
      <ul className="space-y-4">
        {items.slice(0, 5).map((item) => {
          // ğŸ’¡ [í•µì‹¬ ìˆ˜ì •]
          // btoa() ëŒ€ì‹  Node.jsì˜ Bufferë¥¼ ì‚¬ìš©í•˜ì—¬ URL-Safe Base64ë¡œ ì¸ì½”ë”©í•©ë‹ˆë‹¤.
          // 'base64url'ì€ +, / ë¬¸ìë¥¼ -, _ë¡œ ìë™ ë³€í™˜í•˜ê³  íŒ¨ë”©(=)ì„ ì œê±°í•©ë‹ˆë‹¤.
          const safeGuid = Buffer.from(item.guid).toString("base64url");

          return (
            <li
              key={item.guid}
              className="border-b border-gray-800 pb-3 last:border-b-0"
            >
              {/* ğŸ’¡ [í•µì‹¬ ìˆ˜ì •] safeGuidë¥¼ hrefë¡œ ì‚¬ìš©í•©ë‹ˆë‹¤. */}
              <Link
                href={`/feed/${safeGuid}`}
                className="text-gray-300 hover:text-white transition-colors"
              >
                {item.title}
              </Link>

              {/* ìš”ì•½ ë‚´ìš© (contentSnippet) í‘œì‹œ */}
              {item.contentSnippet && (
                <p className="text-sm text-gray-400 mt-2 leading-relaxed">
                  {item.contentSnippet.slice(0, 150)}...
                </p>
              )}

              {/* ì¹´í…Œê³ ë¦¬(íƒœê·¸) í‘œì‹œ */}
              <div className="flex items-center gap-2 mt-3">
                <Tag size={14} className="text-gray-600" />
                <div className="flex gap-2 flex-wrap">
                  {item.categories?.map((tag) => (
                    <span
                      key={tag}
                      className="text-xs px-2 py-1 rounded bg-gray-800 text-gray-400"
                    >
                      {tag}
                    </span>
                  ))}
                </div>
              </div>

              <p className="text-xs text-gray-500 mt-2">
                {new Date(item.pubDate).toLocaleDateString()}
              </p>
            </li>
          );
        })}
      </ul>
    </div>
  );
}
